import React from 'react'
import Grid from '@mui/material/Grid';
import { makeStyles } from "@material-ui/core/styles";

import '../../css/home.css';
import '../../css/retail.css';

import Icon1 from '../../images/media/Media_Icon1.png';
import Icon2 from '../../images/media/Media_Icon2.png';
import Icon3 from '../../images/media/Media_Icon3.png';
import Icon4 from '../../images/media/Media_Icon4.png';
import Icon5 from '../../images/media/Media_Icon5.png';

const useStyles = makeStyles({
    gridContainer: {
      fontFamily:'Montserrat',
      paddingTop:'5em',
      paddingLeft:'50px',
      paddingRight:'50px',
      textAlign: 'center'
    },
});

const MediaBenefits = () => {
    const classes = useStyles();

    return (
        <div className="mont-font benefits-style">
            <div style={{textAlign: 'center'}}>
                <h2 className="yellow-font font-700">AdPrice Benefits:</h2>
            </div>
        
            <Grid container alignItems="flex-start" justifyContent="center" className={classes.gridContainer} spacing={16}>
                <Grid item xs={2}>
                    <img style={{height:'50px'}} src={Icon1} alt="icon1"/>
                    <h6 className="benefits-text">Automatic Recalculations</h6>
                </Grid>
                <Grid item xs={2}>
                    <img style={{height:'50px'}} src={Icon2} alt="icon2"/>
                    <h6 className="benefits-text">Extended Global Reach</h6>
                </Grid>
                <Grid item xs={2}>
                    <img style={{height:'50px'}} src={Icon3} alt="icon3"/>
                    <h6 className="benefits-text">Configurable Integrations</h6>
                </Grid>
                <Grid item xs={2}>
                    <img style={{height:'50px'}} src={Icon4} alt="icon4"/>
                    <h6 className="benefits-text">Higher Win Rates</h6>
                </Grid>
                <Grid item xs={2}>
                    <img style={{height:'50px'}} src={Icon5} alt="icon5"/>
                    <h6 className="benefits-text">Numerous Data Sources</h6>
                </Grid>
            </Grid>
        </div>
    )
}

export default MediaBenefits;
